import React from 'react';
import firebase from 'firebase';
import PropTypes from 'prop-types';
import { Div ,PanelHeader, Panel, PanelHeaderBack, Group, RichCell, Avatar, Header, Separator, InfoRow, Gallery} from '@vkontakte/vkui';
import "@vkontakte/vkui/dist/vkui.css";




const SkinInfo = ({ id, go}) => (
	<Panel id={id}>
		<PanelHeader left={<PanelHeaderBack onClick={go} data-to="def_skins"/>}>Карточка скина</PanelHeader>
        <Group header={<Header mode="secondary">Основная информация</Header>}>
        <RichCell
        before={<Avatar size={48} />}
        text="Айди игрока:"
      >
        Автор скина:
      </RichCell> 
     <Div> <InfoRow header="Комментарий пользователя" >Комментарий пользователя</InfoRow> </Div>
        </Group>
        <Group header={<Header mode="secondary">Галерея</Header>}>
        <Gallery
                slideWidth="90%"
                style={{ height: 150 }}
                bullets="dark"
              >
                <div style={{ backgroundColor: 'var(--destructive)' }} />
                <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />
                <div style={{ backgroundColor: 'var(--accent)' }} />
              </Gallery>
        </Group>
	</Panel>
);


SkinInfo.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default SkinInfo;